@import url(https://fonts.googleapis.com/css2?family=Marck+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.Logo {
  font-family: 'Marck Script', cursive;
  color: white;
  font-size: 30px;
}

.link {
  text-decoration: none;
}
.link:hover {
  text-decoration: none;
}

.HireMe {
  padding: 5px 10px 5px 10px;
  border-radius: 10px;
  border: 1px solid #ffffff;
}

.backgrounds {
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 100%;
  top: 0%;
  left: 0%;
  z-index: -1;
  background-color: #000;
}
.Background1 {
  content: "";
  overflow: hidden;
  display: block;
  position: relative;
  top: -0;
  bottom: -0;
  left: -0;
  right: 0%;
  width: 105%;
  height: 100%;
  background: #000 url(/static/media/laptopBackground.0fa7d462.jpg) center center fixed no-repeat;
  background-color: #000;
  background-size: cover;
  -webkit-filter: blur(5px) grayscale(1) opacity(.5);
          filter: blur(5px) grayscale(1) opacity(.5);
  transform: scale(1.1);
}

.focus {
  font-family: 'Open Sans', sans-serif;
  margin-top: 12%;
  text-align: center;
  color: white;
}

.focus span {
  font-size: 40px;
  font-weight: 100;
}

.go-to-one, .go-to-two {
  text-decoration: none;
  color: white;
}
.go-to-one:hover, .go-to-two:hover {
  cursor: pointer;
  text-decoration: none;
  color: white;
}

.go-to-portfolio {
  margin-top: 1%;
  display: flex;
  width: 100%;
  justify-content: center;
  flex-wrap: wrap;
}
.go-to-portfolio-flex{
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
.go-to-portfolio-flex:hover {
  cursor: pointer;
}

.portfolio {
  margin-top: 50%;
  color: black;
}

.ProjectsFlex {
  display: flex;
  justify-content: center;
  
}

.text-center {
  margin: 10px;
  padding: 0px;
}

.ContactFormHolder {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}
.ContactForm {
  border: solid 1px rgba(100, 100, 100, 1);
  border-radius: 5px;
  padding: 10px;
  width: 75%;
}

@keyframes blinking {
  from {opacity: 0;}
  to {opacity: 1;}
}

.Cursor--blinking {
  animation: blinking 1s;
  animation-iteration-count: infinite;
}
